.home-container{
    background-color:white;
    height: 100vh ;
    display: grid;
    grid-template-columns: 2fr 3fr;
    .left-container{
        background-color: rgb(19,18,18);
        display: flex;
        justify-content:  center;
        align-items: center;
    }
    .items-container{
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 15px;
        img{
            width: 100px;
        }
        h2{
            color: rgb(240, 228, 228);
        }
        button{
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            height: 50px;
            width: 90%;
            border-radius: 25px;
            border: none;
            transition-duration: 1s;
            font-size: 20px;
        }
        button:hover{
            box-shadow: 2px 2px 6px gray;
            
        }



    }
}